import { _decorator, Component, Node } from "cc";
import { QRCode, QRErrorCorrectLevel } from "./QRCode";
import * as cc from "cc";
const { ccclass, property } = _decorator;

@ccclass("test")
export class test extends Component {
  ctxNode: Node;

  start() {
    let ctx = this.node.getComponent(cc.Graphics);
    if (!ctx) {
      ctx = this.node.addComponent(cc.Graphics);
    }

    this.QRCreate(
      ctx,
      "https://docs.cocos.com/creator/3.6/manual/zh/ui-system/components/editor/graphics.html"
    );
  }

  //绘制二维码
  QRCreate(ctx, url) {
    var qrcode = new QRCode(-1, QRErrorCorrectLevel.H);
    qrcode.addData(url);
    qrcode.make();

    ctx.fillColor = cc.Color.BLACK;
    let roll = Math.floor(qrcode.getModuleCount());
    //块宽高
    var tileW = this.node.getComponent(cc.UITransform).width / roll;
    var tileH = this.node.getComponent(cc.UITransform).height / roll;
    // draw in the Graphics
    for (var row = 0; row < qrcode.getModuleCount(); row++) {
      for (var col = 0; col < qrcode.getModuleCount(); col++) {
        if (qrcode.isDark(row, col)) {
          var w = Math.ceil((col + 1) * tileW) - Math.floor(col * tileW);
          var h = Math.ceil((row + 1) * tileW) - Math.floor(row * tileW);
          let posx =
            Math.round(col * tileW) -
            this.node.getComponent(cc.UITransform).width / 2;
          let posy =
            Math.round(row * tileH) -
            this.node.getComponent(cc.UITransform).height / 2;

          ctx.rect(posx, posy, w, h);
          //颜色
          ctx.fillColor = cc.Color.WHITE;
          ctx.fill();
        }
      }
    }
  }

  update(deltaTime: number) {}
}
